#groupfolders-react-root .group-edit {
  min-width: 300px;
  top: 0;
  position: absolute;
  background-color: white;
  z-index: 10;
  border: 1px solid #ddd;
}

  #groupfolders-react-root .group-edit td {
    padding: 2px 10px;
  }

  #groupfolders-react-root .group-edit td:last-child > a {
    float: right;
  }

  #groupfolders-react-root .group-edit td:first-child, #groupfolders-react-root .group-edit th:first-child {
    min-width: 170px;
  }

  #groupfolders-react-root .group-edit td:last-child, #groupfolders-react-root .group-edit th:last-child {
    width: 16px;
  }

  #groupfolders-react-root .group-edit td.permissions {
    text-align: center;
  }

  #groupfolders-react-root .group-edit input {
    margin: 0;
  }

  #groupfolders-react-root .group-edit img {
    opacity: 0;
    -webkit-transition: opactity 0.5s;
    transition: opactity 0.5s;
  }

  #groupfolders-react-root .group-edit tr:hover img {
    opacity: 0.5;
  }

  #groupfolders-react-root .group-edit tr:hover img:hover {
    opacity: 1;
  }

  #groupfolders-react-root .group-edit tr {
  }
