#groupfolders-react-root td.quota {
  position: relative;
  padding: 0;
}

  /** complex query to ensure we overwrite the core style */

  #groupfolders-react-root td.quota input.editselect-input:not([type='range']):not(:disabled):not(.primary):not(#quota).error, #groupfolders-react-root td.quota input.editselect-input:not([type='range']):not(:disabled):not(.primary):not(#quota).error:focus, #groupfolders-react-root td.quota input.editselect-input:not([type='range']):not(:disabled):not(.primary):not(#quota).error:hover {
  border-color: red;
}

  #groupfolders-react-root td.quota input.editselect-input:not([type='range']):not(:disabled):not(.primary):not(#quota) {
  width: 150px;
  position: absolute;
  top: 0;
  left: 0;
}

  #groupfolders-react-root td.quota select.editselect {
    height: 34px;
    width: 150px;
    position: absolute;
    top: 0;
    left: 0;
  }

  #groupfolders-react-root td.quota .quotabar-holder {
    height: 34px;
    width: 150px;
    position: relative;
  }

  #groupfolders-react-root td.quota .quotabar {
    position: absolute;
    margin: 3px 0;
    border-radius: 3px;
    top: 0;
    left: 0;
    background-color: #eee;
    height: 34px;
  }
