#groupfolders-react-root, #groupfolders-root {
  height: 100%;
  min-height: calc(100vh - 50px);
}

#groupfolders-react-root, #groupfolders-react-root > table {
  width: 100%;
}

#groupfolders-react-root tr {
    height: 32px;
}

#groupfolders-react-root th {
    border-bottom: 1px #ddd solid;
    cursor: pointer;
}

#groupfolders-react-root th .sort_arrow {
      float: right;
      color: #888;
    }

#groupfolders-react-root td, #groupfolders-react-root th {
    padding: 10px;
    position: relative;
    display: table-cell;
}

#groupfolders-react-root td > form, #groupfolders-react-root th > form {
  margin: -10px;
}

#groupfolders-react-root td > form input[type='text'], #groupfolders-react-root th > form input[type='text'] {
        width: 100%;
}

#groupfolders-react-root td .action-rename::after, #groupfolders-react-root th .action-rename::after {
  display: inline-block;
  position: relative;
  top: 2px;
  left: 2px;
  content: '';
  width: 16px;
  height: 16px;
  background: url(data:image/svg+xml;base64,) no-repeat center;
  opacity: 0;
}

#groupfolders-react-root td .action-rename::after:hover, #groupfolders-react-root th .action-rename::after:hover {
  opacity: 1;
}

#groupfolders-react-root td.groups, #groupfolders-react-root th.groups {
  width: 400px;
}

#groupfolders-react-root td.remove, #groupfolders-react-root th.remove {
  width: 32px;
}

#groupfolders-react-root td a.icon, #groupfolders-react-root th a.icon {
      margin-left: 5px;
}

#groupfolders-react-root tr:hover .icon {
      opacity: 0.5
    }

#groupfolders-react-root tr:hover .icon:hover {
  opacity: 1;
}

#groupfolders-react-root tr:hover .action-rename::after {
  opacity: 0.5;
}

#groupfolders-react-root tr:hover .action-rename:hover::after {
  opacity: 1;
}

#groupfolders-react-root .icon {
    display: inline-block;
    opacity: 0;
  //transition: opacity 0.5s;
}

#groupfolders-react-root .icon.icon-visible {
  opacity: 0.5;
}

#groupfolders-react-root .newgroup-name {
    width: 200px;
    max-width: calc(100% - 40px);
}
